
export const scroll={

  scrollTopBar(){
    let long=200;

    let box = document.getElementById('root'); //å¤–é¢çš„å®¹å™¨ã€‚
    let listBox = document.getElementById('list'); //ulåˆ—è¡¨ã€‚ä¸»è¦æ˜¯ç§»åŠ¨å®ƒçš„leftå€¼
    let list = document.getElementsByClassName('ivu-tag');//æ‰€æœ‰åˆ—è¡¨å…ƒç´ 
    let width = box.clientWidth /4*3;  //ä¸ºäº†åˆ¤æ–­æ˜¯å·¦æ»‘è¿˜æ˜¯å³æ»‘
    let totalWidth = 0;
    for(let i=0;i<list.length;i++){
      totalWidth = totalWidth + list[i].offsetWidth; //æ‰€æœ‰åˆ—è¡¨å…ƒç´ çš„æ€»å®½åº¦
    }
    for(let i=0;i<list.length;i++){
      let _offset = totalWidth - box.clientWidth; //å³è¾¹çš„åç§»é‡

      list[i].addEventListener('click', function (e) {

        /*for(let j=0;j<list.length;j++){
         list[j].className = 'off ivu-tag ivu-tag-default ivu-tag-closable ivu-tag-checked';  //ç§»é™¤æ‰€æœ‰å…ƒç´ çš„æ ·å¼
        }
        list[i].className = 'on ivu-tag ivu-tag-red ivu-tag-closable ivu-tag-checked';   //ç»™ç‚¹å‡»çš„å…ƒç´ æ·»åŠ æ ·å¼
*/

        let offset =totalWidth - (Math.abs(listBox.offsetLeft) + box.clientWidth)+100; //å³è¾¹çš„åç§»é‡ = æ‰€æœ‰å…ƒç´ å®½åº¦ä¹‹å’Œ - ï¼ˆulå®¹å™¨çš„å·¦åç§»é‡ + çˆ¶å®¹å™¨çš„å®½åº¦ï¼‰

        //console.log("width "+width )
        //console.log("e.pageX "+e.pageX )
        //console.log("offset "+offset )
        //console.log("listBox.offsetLeft "+listBox.offsetLeft )

        if(e.pageX > width && offset >0){  //ç‚¹å‡»å³ä¾§å¹¶ä¸”å³ä¾§çš„åç§»é‡å¤§äºŽ0ï¼Œå³æ»‘ã€‚
          listBox.style.left = (listBox.offsetLeft-long) + 'px';
        }else if(e.pageX > width && offset > long){ //ä¸´ç•Œä½ç½®ï¼Œï¼Œå³ä¾§æ»šåŠ¨åˆ°æœ«å°¾
          listBox.style.left = -_offset + 'px';
        }
        if(e.pageX < width && listBox.offsetLeft < -200) { //ç‚¹å‡»å·¦ä¾§å¹¶ä¸”å·¦ä¾§çš„åç§»é‡å°äºŽ0ï¼Œå·¦æ»‘ã€‚
          listBox.style.left = (listBox.offsetLeft + long) + 'px';

        }else if(e.pageX < width && listBox.offsetLeft < 0){ //ä¸´ç•Œä½ç½®ï¼Œå·¦ä¾§æ»šåˆ°å¼€å§‹çš„ä½ç½®
          listBox.style.left = 0
        }

      });

    }
  }



}



